import { useState, useRef, useEffect } from "react";
import { useNavigate } from "react-router";
import { Button, Form } from "react-bootstrap";
import searchIconSm from "../../assets/images/new/search-icon-sm.svg";
import search from "../../assets/images/new/search.svg";
import axios from "axios";
import { serverPath } from "../../config/key";
import Storage from "../../utils/storage";
import { useContext } from "react";
import AppContext from "../../context/appContext";

const SearchComponent = () => {
  const { setSingleConsulting } = useContext(AppContext);
  const lang = Storage.get("lang")?.apiShortCode;
  const navigate = useNavigate();
  const [filteredData, setFilteredData] = useState([]);
  const [searchText, setSearchText] = useState("");
  const [searchResult, setSearchResult] = useState(false);
  const [categoriesData, setcategoriesData] = useState([]);

  const wrapperRef = useRef(null);
  useOutsideAlerter(wrapperRef);

  function useOutsideAlerter(ref) {
    useEffect(() => {
      function handleClickOutside(event) {
        if (ref.current && !ref.current.contains(event.target)) {
          setSearchResult(false);
        }
      }

      document.addEventListener("mousedown", handleClickOutside);
      return () => {
        document.removeEventListener("mousedown", handleClickOutside);
      };
    }, [ref]);
  }

  const getAllConsultingFields = async () => {
    try {
      const response = await axios.get(
        `${serverPath}/consultation/getAllConsultation`
      );
      const {
        data: { code, result },
      } = response || {};

      if (code === 200) {
        setcategoriesData(result);
      }
    } catch (error) {
      console.log("error", error);
    }
  };

  const handleSearch = (e) => {
    setSearchResult(true);
    const searchWord = e.target.value;
    setSearchText(searchWord);
    const newFilter = categoriesData.filter((value) => {
      return lang === "eng"
        ? value.eng.toLowerCase().includes(searchWord.toLowerCase())
        : value.heb.toLowerCase().includes(searchWord.toLowerCase());
    });

    if (searchWord === "") {
      setFilteredData("");
    } else {
      setFilteredData(newFilter);
    }
  };
  const handleSelectResult = (text) => {
    setSearchText(text);
    setSearchResult(false);
  };
  const handleRedirect = () => {
    if (searchText !== "") {
      navigate("/consulting-detail");
      setSingleConsulting(searchText);
    }
  };

  useEffect(() => {
    getAllConsultingFields();
  }, []);
  const boldQuery = (item) => {
    const text = lang === "eng" ? item?.eng : item?.heb;
    const termSp = searchText.split("");
    const textSp = text.split("");
    const result = textSp.map((el) => {
      if (termSp.includes(el.toLowerCase())) {
        return `<b>${el}</b>`;
      }
      return el;
    });
    return result.join("");
  };

  return (
    <div className="search-input" ref={wrapperRef}>
      <Form onSubmit={handleRedirect}>
        <Form.Group className="search-input-form">
          <Form.Control
            type="search"
            placeholder=" ..."
            value={searchText}
            onChange={handleSearch}
          />
          <img
            src={search}
            alt="search"
            className="search-icon"
            width={24}
            height={24}
          />
          <Button
            className="black-btn"
            type="submit"
            disabled={filteredData?.length === 0}
          >
            <img src={searchIconSm} alt="search" width={18} height={18} />
          </Button>
        </Form.Group>
        {searchResult && searchText !== "" ? (
          <SearchResultComponent
            filteredData={filteredData}
            boldQuery={boldQuery}
            searchText={searchText}
            handleSelectResult={handleSelectResult}
            lang={lang}
          />
        ) : null}
      </Form>
    </div>
  );
};

export default SearchComponent;

const SearchResultComponent = ({
  filteredData,
  boldQuery,
  handleSelectResult,
  lang,
}) => {
  return (
    <div className="search-result-section">
      <ul>
        {filteredData && filteredData?.length > 0 ? (
          filteredData.map((data, i) => {
            return (
              <li
                dangerouslySetInnerHTML={{
                  __html: `${boldQuery(data)}`,
                }}
                key={i}
                onClick={() => handleSelectResult(data?.[lang])}
              />
            );
          })
        ) : (
          <li>no result</li>
        )}
      </ul>
    </div>
  );
};
